export class Constant {
    public static get VOTES_COUNT(): number { return 360 };
    public static get PETITIONS_STATUS(): string[] {
        return ['', ' ', '', ' ', ' ']
    };

    public static STATUS_COLOR(status: number): any {
        switch (status) {
            case 0:
                return '#c1afb1';
            case 1:
                return '#1ba1e2';
            case 2:
                return '#ffb410';
            case 3:
                return '#028900';
            case 4:
                return '#ff0065';
        }
    };

    public static STATUS_NAME(status: number): string {
        switch (status) {
            case 0:
                return '';
            case 1:
                return ' ';
            case 2:
                return '';
            case 3:
                return ' ';
            case 4:
                return ' ';
            default:
                return '';
        }
    };

    public static STATUS_VALUE(name: string): number {
        switch (name) {
            case '':
                return 0;
            case ' ':
                return 1;
            case '':
                return 2;
            case ' ':
                return 3;
            case ' ':
                return 4;
            default:
                return -1;
        }
    }
}


